import React, {Component} from 'react';
import BgImage from '../assets/images/bg_2.jpg';
import '../assets/css/style.css';
const bgstyle = {
    backgroundImage: `url(${BgImage})`
}


class Home extends Component{
    render(){
        return(
            <div>
                <section className="hero-wrap js-fullheight" style={bgstyle} >
                    <div className="overlay"></div>
                    <div className="container">
                        <div className="row no-gutters slider-text js-fullheight align-items-center justify-content-end" data-scrollax-parent="true">
                            <div className="col-lg-6 ">
                                <div className="mt-5">
                                    <h1 className="mb-4">The Most Valuable Thing is Your Health</h1>
                                    <p className="mb-4">Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts. Separated they live in Bookmarksgrove.</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section class="ftco-section ftco-no-pt ftco-no-pb ftco-services-2 bg-light">
                    <div class="container">
                        <div class="row d-flex">
                            <div class="col-md-7 py-5">
                                <div class="py-lg-5">
                                    <div class="row justify-content-center pb-5">
                                        <div class="col-md-12 heading-section ">
                                            <h2 class="mb-3">Welcome to <span>Medex</span></h2>
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-md-12 col-lg-6 d-flex align-self-stretch ">
                                            <div class="media block-6 services d-flex">
                                                <div class="icon justify-content-center align-items-center d-flex"><span class="flaticon-ambulance"></span></div>
                                                <div class="media-body pl-md-4">
                                                    <h3 class="heading mb-3">Emergency Help</h3>
                                                    <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                                </div>
                                            </div>      
                                        </div>
                                        <div class="col-md-12 col-lg-6 d-flex align-self-stretch ">
                                            <div class="media block-6 services d-flex">
                                                <div class="icon justify-content-center align-items-center d-flex"><span class="flaticon-ophthalmologist"></span></div>
                                                <div class="media-body pl-md-4">
                                                    <h3 class="heading mb-3">Qualified Doctors</h3>
                                                    <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                                </div>
                                            </div>      
                                        </div>
                                        <div class="col-md-12 col-lg-6 d-flex align-self-stretch ">
                                            <div class="media block-6 services d-flex">
                                                <div class="icon justify-content-center align-items-center d-flex"><span class="flaticon-flag"></span></div>
                                                <div class="media-body pl-md-4">
                                                    <h3 class="heading mb-3">Location &amp; Directions</h3>
                                                    <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                                </div>
                                            </div>      
                                        </div>
                                        <div class="col-md-12 col-lg-6 d-flex align-self-stretch ">
                                            <div class="media block-6 services d-flex">
                                                <div class="icon justify-content-center align-items-center d-flex"><span class="flaticon-stethoscope"></span></div>
                                                <div class="media-body pl-md-4">
                                                    <h3 class="heading mb-3">Medical Treatment</h3>
                                                    <p>A small river named Duden flows by their place and supplies it with the necessary regelialia.</p>
                                                </div>
                                            </div>      
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-5 d-flex">
                                <div class="appointment-wrap p-4 p-lg-5 d-flex align-items-center">
                                    <form action="#" class="appointment-form ">
                                        <h3>Appointment Form</h3>
                                        <div class="">
                                            <div class="form-group">
                                                <input type="text" class="form-control" placeholder="First Name"/>
                                            </div>
                                            <div class="form-group">
                                                <input type="text" class="form-control" placeholder="Last Name"/>
                                            </div>
                                        </div>
                                        <div class="">
                                            <div class="form-group">
                                                <div class="form-field">
                                                    <div class="select-wrap">
                                                        <div class="icon"><span class="fa fa-chevron-down"></span></div>
                                                        <select name="" id="" class="form-control">
                                                            <option value="">Select Your Services</option>
                                                            <option value="">Neurology</option>
                                                            <option value="">Cardiology</option>
                                                            <option value="">Dental</option>
                                                            <option value="">Ophthalmology</option>
                                                            <option value="">Other Services</option>
                                                        </select>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="form-group">
                                                <input type="text" class="form-control" placeholder="Phone"/>
                                            </div>
                                        </div>
                                        <div class="">
                                            <div class="form-group">
                                                <div class="input-wrap">
                                                    <div class="icon"><span class="fa fa-calendar"></span></div>
                                                    <input type="text" class="form-control appointment_date" placeholder="Date"/>
                                                </div>
                                            </div>
                                            <div class="form-group">
                                                <div class="input-wrap">
                                                    <div class="icon"><span class="fa fa-clock-o"></span></div>
                                                    <input type="text" class="form-control appointment_time" placeholder="Time"/>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="">
                                            <div class="form-group">
                                                <textarea name="" id="" cols="30" rows="2" class="form-control" placeholder="Message"></textarea>
                                            </div>
                                            <div class="form-group">
                                                <input type="submit" value="Appointment" class="btn btn-secondary py-3 px-4"/>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default Home;